import { registerPlugin } from '@capacitor/core';

import type { StreamVideoCallCapacitorPlugin } from './definitions';

const StreamVideoCallCapacitor = registerPlugin<StreamVideoCallCapacitorPlugin>('StreamVideoCallCapacitor', {
  web: () => import('./web').then((m) => new m.StreamVideoCallCapacitorWeb()),
});

export * from './definitions';
export { StreamVideoCallCapacitor };
